<style lang="scss">
.icon_block {
    display: block;
}

.icon_lists {
    width: 100% !important;
    overflow: hidden;
    *zoom: 1;
}

.icon_lists li {
    display: inline-block;
    width: 100px;
    margin-bottom: 10px;
    margin-right: 20px;
    text-align: center;
    list-style: none !important;
    cursor: default;
    vertical-align: top;
    .name {
        display: none;
    }
}

.icon_lists li .code-name {
     line-height: 1.2;
     font-size: 12px;
}

.icon_lists .icon {
    display: block;
    height: 100px;
    line-height: 100px;
    font-size: 42px;
    margin: 10px auto;
    color: #333;
    -webkit-transition: font-size 0.25s linear, width 0.25s linear;
    -moz-transition: font-size 0.25s linear, width 0.25s linear;
    transition: font-size 0.25s linear, width 0.25s linear;
}

.icon_lists .icon:hover {
    font-size: 100px;
}

.icon_lists li .name,
.icon_lists li .code-name {
    color: #666;
}
</style>